@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,600;1,500&display=swap);
/*
=============== 
working in refactor
===============
*/
:root {
  /* color  primary backgroud */
  --clr-primary-color-bg: radial-gradient(
    hsl(214, 47%, 23%),
    hsl(237, 49%, 15%)
  );
  /* color for elements game */
  --clr-scissors-gradient: radial-gradient(hsl(39, 89%, 49%),hsl(40, 84%, 53%));
  --clr-paper-gradient: radial-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
  --clr-rock-gradient: radial-gradient(hsl(349, 71%, 52%), hsl(349, 70%, 56%));
  --clr-lizard-gradient: radial-gradient(hsl(261, 73%, 60%),hsl(261, 72%, 63%));
  --clr-cyan-gradient: radial-gradient(hsl(189, 59%, 53%), hsl(189, 58%, 57%));

  /* grey used for paragraphs */
  --clr-dark-text: hsl(229, 25%, 31%);
  --clr-score-text: hsl(229, 64%, 46%);
  --clr-header-outline: hsl(217, 16%, 45%);
  --clr-stick: #182741;
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);

  /* general styles */
  --font-size-detail-page: 1rem;
  --font-size-home-page-items: 15px;
  --font-weight-1: 600;
  --font-weight-2: 700;
  --max-width: 1366px;
  --light-shadow-invert: inset 0 7px 2px hsla(240, 1%, 77%, 0.904);
  --light-shadow: inset 0px -7px 2px hsla(0, 0%, 0%, 0.404);
  --radius: 0.25rem;
  --transition: all 0.3s linear;
}

/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Barlow Semi Condensed", sans-serif;
  color: hsl(210, 36%, 96%);
  color: var(--clr-grey-10);
  background: radial-gradient(hsl(214, 47%, 23%), hsl(237, 49%, 15%));
  line-height: 1.5;
  font-size: 15px;
  font-size: var(--font-size-home-page-items);
  min-height: 100vh;
  transition: all 0.3s linear;
  overflow-x: hidden;
}

h1 {
  font-size: 2rem;
  text-transform: uppercase;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: black;
}

p {
  text-overflow: ellipsis;
}

.container {
  width: 80vw;
  margin: 0 auto;
  max-width: 1366px;
  max-width: var(--max-width);
}

