===============
*/

/*
=============== 
Header
===============
*/

.header {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin: 0px auto;
  border: hsl(217, 16%, 45%) 4px solid;
  border: var(--clr-header-outline) 4px solid;
  border-radius: 10px;
  padding: 20px;
  margin-top: 40px;
}

.header .title {
  width: 150px;
  line-height: 0.8;
}

.score {
  background: hsl(210, 36%, 96%);
  background: var(--clr-grey-10);
  border-radius: 10px;
  width: 180px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.score-text {
  color: hsl(229, 64%, 46%);
  color: var(--clr-score-text);
  text-transform: uppercase;
}

.score-number {
  color: hsl(229, 25%, 31%);
  color: var(--clr-dark-text);
  font-size: 4rem;
}

