/*
=============== 
ON Game
===============
*/

.ongame {
  height: 500px;
  width: 800px;
  margin-top: 50px;
  display: grid;
  grid-template-columns: 300px 100px 100px 300px;
  grid-template-rows: auto;
  grid-template-areas: "owncircle result result youcircle";
}

.rules {
  display: flex;
  justify-content: flex-end;
}

.btn {
  border-radius: 0.25rem;
  border-radius: var(--radius);
  background: inherit;
  border: 1px solid white;
  width: 100px;
  padding: 10px 25px;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  transition: all ease-in-out 0.2s;
}

.btn:hover {
  background: hsl(210, 36%, 96%);
  background: var(--clr-grey-10);
  color: hsl(229, 25%, 31%);
  color: var(--clr-dark-text);
}

.circle-you-picked {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  box-shadow: inset 0px -7px 2px hsla(0, 0%, 0%, 0.404);
  box-shadow: var(--light-shadow);
}

.circle-you-picked .circle-int {
  top: 18px;
  left: 19px;
  position: relative;
  background: hsl(210, 36%, 96%);
  background: var(--clr-grey-10);
  width: 210px;
  height: 210px;
  border-radius: 50%;
  box-shadow: inset 0 7px 2px hsla(240, 1%, 77%, 0.904);
  box-shadow: var(--light-shadow-invert);
}

.circle-you-picked img {
  position: relative;
  border: none;
  background: none;
  top: 50px;
  left: 70px;
  height: 100px;
  width: 80px;
  transform: rotateY(0deg);
  transition: all 0.3s linear;
  transition: var(--transition);
}

.circle-you-picked img.win,
.circle-you-picked img.draw {
  transform: rotateY(360deg);
}
.circle-you-picked img.lose {
  transform: rotateY(0deg);
}

.you-picked {
  grid-area: owncircle;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-picked {
  grid-area: youcircle;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.you-picked h2 {
  text-align: center;
  margin-bottom: 50px;
}

.home-picked h2 {
  text-align: center;
  margin-bottom: 50px;
}

.results {
  display: flex;
  grid-area: result;
  z-index: 1;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 100px;
  transform: translateY(50px);
  transition: all 0.3s linear;
  transition: var(--transition);
}

.results h1 {
  font-size: 2.9rem;
}

.results .btn-play {
  background: hsl(210, 36%, 96%);
  background: var(--clr-grey-10);
  width: 100%;
  height: 50px;
  border-radius: 0.25rem;
  border-radius: var(--radius);
  border: none;
  color: hsl(229, 25%, 31%);
  color: var(--clr-dark-text);
  font-size: 1.1rem;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.results .btn-play:hover {
  width: 100%;
  height: 50px;
  border-radius: 0.25rem;
  border-radius: var(--radius);
  border: none;
  color: hsl(349, 71%, 52%);
  font-size: 1.1rem;
}

.brightness {
  display: block;
  position: absolute;
  z-index: -2;
  height: 550px;
  width: 550px;
  border-radius: 50%;
  background-color: rgba(168, 168, 168, 0.116);
  transform: scale(0);
  transition: all ease-in-out 0.3s;
}

.brightness.end-game.win {
  top: -160px;
  left: -170px;
}

.brightness.end-game.lose {
  display: none;
}

.brightness.end-game.draw {
  display: none;
}

.brightness.end-game {
  transform: scale(1);
  animation: bounce 2s ease-in-out infinite;
}

.brightness span {
  position: absolute;
  z-index: -2;
  top: 100px;
  left: 100px;
  height: 350px;
  width: 350px;
  border-radius: 50%;
  background-color: rgba(168, 168, 168, 0.116);
}

.brightness span::before {
  content: "";
  z-index: -2;
  position: absolute;
  top: -50px;
  left: -50px;
  height: 450px;
  width: 450px;
  border-radius: 50%;
  background-color: rgba(168, 168, 168, 0.116);
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: grid;
  place-items: center;
  opacity: 0;
  z-index: -1;
  transition: all 0.3s linear;
  transition: var(--transition);
}

/*
=============== 
Modal Rules
===============
*/
.show-modal {
  opacity: 1;
  z-index: 10;
}

.modal-container {
  background: hsl(210, 36%, 96%);
  background: var(--clr-grey-10);
  border-radius: 0.25rem;
  border-radius: var(--radius);
  width: 50vw;
  max-width: var(--fixed-width);
  text-align: left;
  display: flex;
  flex-direction: column;
  place-items: center;
  position: relative;
}

.modal-container h1 {
  color: hsl(229, 25%, 31%);
  color: var(--clr-dark-text);
}

.modal-container div {
  width: 100%;
  padding: 20px;
}

.header-modal-container {
  display: flex;
  justify-content: space-between;
}

.image-modal-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-modal-container img {
  width: 70%;
  height: 70%;
}

.close-modal-btn {
  font-size: 2rem;
  background: transparent;
  border-color: transparent;
  color: hsl(210, 1%, 64%);
  cursor: pointer;
}

